
import { defineComponent, PropType, reactive, ref } from 'vue'
import type { FormRules } from 'element-plus'
import { IFilterGroupForm } from '@/types'
import FormMixin from '@/mixins/form.mixin'

export interface IData {
  model: IFilterGroupForm,
  isValid: boolean;
}

export default defineComponent({
  name: 'FilterGroupForm',

  mixins: [FormMixin],

  setup () {
    return {
      rules: reactive<FormRules>({})
    }
  },

  props: {
    modelValue: Object as PropType<IFilterGroupForm>,

    isLoading: Boolean as PropType<boolean>
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || {
        location: '',
        sortOrder: 0,
        name: '123',
        filters: []
      },

      isValid: false
    }
  },

  methods: {
    addFilter () {
      this.model.filters.push({
        sortOrder: 0,
        name: ''
      })
    },

    removeFilter (index: number) {
      this.model.filters.splice(index, 1)
    }
  }
})
