
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IFilterGroupForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import FilterGroupForm from '@/views/catalog/filter/forms/FilterGroupForm.vue'

export interface IData {
  model: IFilterGroupForm;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IFilterGroupForm) => {
      return store.dispatch('filterGroup/createForm', dataForm)
    }) as ResponseFetching<Promise<IFilterGroupForm>>

    return {
      saveForm,
      saveStatus,
      saveError,
      form: ref()
    }
  },

  components: {
    PageCard,
    FilterGroupForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        location: '',
        sortOrder: 0,
        name: 'New filter group',
        filters: []
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'filter' })
    },

    async handleSubmit () {
      // if (this.form && await this.form.validate()) {
      // }
      const data: IFilterGroupForm = await this.saveForm(this.model)
      if (typeof data !== 'undefined') {
        this.model = data
      }

      if (this.model?.id) {
        this.$router.push({
          name: 'filter-update',
          params: { id: this.model.id }
        })
      }
    }
  }
})
